import React, { useEffect, useState } from "react";
import SidebarSyllabus from "./Sidebar/SideBar";
import SyllabusBasic from "./Syllabus_Contents/Syllabus_Basic";
import SyllabusDescription from "./Syllabus_Contents/Syllabus_Description";
import SyllabusGoals from "./Syllabus_Contents/Syllabus_Goals";
import SyllabusStandardOutput from "./Syllabus_Contents/Syllabus_StandardOutput";
import SyllabusPlanTheory from "./Syllabus_Contents/Syllabus_PlanTheory";
import SyllabusPlanPractice from "./Syllabus_Contents/Syllabus_PlanPractice";
import SyllabusEvaluation from "./Syllabus_Contents/Syllabus_Evaluation";
import SyllabusResources from "./Syllabus_Contents/Syllabus_Resources";
import SyllabusRules from "./Syllabus_Contents/Syllabus_Rules";
import { useHistory, useLocation, useParams } from "react-router";
import "./Syllabus.css";
import "./Sidebar/Sidebar.css";
import useSidebar from "stores/common/SidebarStore/SidebarStore";
const Syllabus = (props) => {
    const [stateSideBar, actionSideBar] = useSidebar();
    const location = useLocation();
    const history = useHistory();
    const params = useParams();
    const [tabIndex, setTabIndex] = useState(null);
    const handleClickChooseTabIndex = (index) => {
        if (index) {
            setTabIndex(index);
            if (stateSideBar?.isOpenSideBar) {
                actionSideBar.setIsOpenSideBar({ isOpenSideBar: false });
            }
        }
    };
    const menus = [
        {
            key: "basic",
            render: () => <SyllabusBasic idSyllabus={params?.id} />,
        },
        {
            key: "description",
            render: () => <SyllabusDescription idSyllabus={params?.id} />,
        },
        {
            key: "goals",
            render: () => <SyllabusGoals idSyllabus={params?.id} />,
        },
        {
            key: "standard-output",
            render: () => <SyllabusStandardOutput idSyllabus={params?.id} />,
        },
        {
            key: "plan-theory",
            render: () => <SyllabusPlanTheory idSyllabus={params?.id} />,
        },
        {
            key: "plan-practices",
            render: () => <SyllabusPlanPractice idSyllabus={params?.id} />,
        },
        // {  key: 'evaluation', render: () =>  <SyllabusEvaluation idSyllabus={params?.id}/> },
        {
            key: "resources",
            render: () => <SyllabusResources idSyllabus={params?.id} />,
        },
        {
            key: "rules",
            render: () => <SyllabusRules idSyllabus={params?.id} />,
        },
    ];
    const renderContent = () => {
        if (tabIndex) {
            const result = menus.find((item) => item?.key?.includes(tabIndex));
            if (result) {
                return result?.render();
            }
        }
        return <SyllabusBasic idSyllabus={params?.id} />;
    };
    useEffect(() => {
        const { hash = "" } = location;
        if (!hash) {
            setTabIndex("basic");
            history.push("#basic");
        } else {
            setTabIndex(hash?.substring(1));
        }
    }, []);
    return (
        <div className="syllabus__container">
            <div className="syllabus__row">
                <div className="syllabus__col">
                    <div className="syllabus__sidebar">
                        <SidebarSyllabus
                            handleClickChooseTabIndex={
                                handleClickChooseTabIndex
                            }
                            tabIndex={tabIndex}
                        />
                    </div>
                    <div className="syllabus__contents">{renderContent()}</div>
                </div>
            </div>
        </div>
    );
};

export default Syllabus;
